.education-header {
  border-bottom: 1px solid #d1d8e1;
  border-top: 1px solid #d1d8e1;

  &.fixed {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    box-shadow: 0px 6px 6px rgba(0, 0, 0, 0.03);
    background-color: #fff;
    border-bottom: none;
    z-index: 5;
  }
}

.education-header__content {
  padding: 16px 0;
  display: flex;
  align-items: center;
}

.education-header__logo {
  font-size: 0;
  line-height: 0;
  margin-right: 60px;

  img {
    width: 108px;
    height: auto;
  }
}

.education-header__nav-list {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.education-header__nav-item {
  &:not(:last-child) {
    margin-right: 40px;
  }
}

.education-header__nav-link {
  @include font(14px, 21px, 400, $text-minor);
  transition: color 0.2s;

  &:hover {
    color: $primary;
  }
}
